export default {
  props: {
    makeLogin: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
  },
  data: () => ({
    value: String,
    step: 1,
    valid: false,
    email: '',
    password: '',
    emailRules: [
      (v) => !!v || 'E-Mail ist erforderlich',
      (v) =>
        /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/.test(v) ||
        'E-Mail muss gültig sein',
    ],
    loading: false,
    showConfirmation: false,
    requestPassword: false,
    showRegister: false,
    buttonText: 'Registrieren',
    error: {
      status: false,
      message: '',
    },
  }),
  mounted() {
    const email = window.localStorage.getItem('emailForSignIn')
    if (email) {
      this.email = email
      this.showConfirmation = true
    }
  },
  methods: {
    next() {
      if (!this.$refs.form.validate()) return
      this.showConfirmation = false
      this.$nuxt.$fire.auth
        .fetchSignInMethodsForEmail(this.email)
        .then((loginMethods) => {
          if (loginMethods.length > 1) {
            this.requestPassword = true
          } else {
            this.requestPassword = false
          }
          this.step = 2
        })
    },
    login() {
      this.loading = true
      if (process.client) {
        this.$store
          .dispatch('modules/user/login', {
            email: this.email,
            password: this.password,
          })
          .then((e) => {
            this.loading = false
          })
          .catch((err) => {
            this.loading = false
            this.error.status = true
            this.error.message = err.message
          })
      }
    },
    register() {
      // if (!this.$refs.form.validate()) return
      this.step = 3
      this.loading = true

      this.$fire.auth
        .sendSignInLinkToEmail(this.email, {
          url: this.$config.baseUrl + '/registration/confirm-register-link',
          handleCodeInApp: true,
        })
        .then(() => {
          this.buttonText = 'Gesendet'
          this.valid = false
          this.loading = false
          this.showConfirmation = true
          window.localStorage.setItem('emailForSignIn', this.email)
        })
    },
  },
}
